 
.header-li-item {
  display: flex;
  width: 100%;
}
.name-text {
  width:20%;
  border: 1px solid #222;
  padding: 12px;
  background-color: #E2E5E8;
  margin: 0;
}
.mobile-no-text {
  width:40%;
  border: 1px solid #222;
  padding: 12px;
  background-color: #E2E5E8;
  margin: 0;
}
.contacts-text {
  padding-left: 40px;
}
.input-field {
  height: 30px;
  width: 250px;
  margin: 10px;
}
.form-style {
  padding-left: 23px;
  display: flex;
}
.submit-btn {
  background-color: lightblue;
  padding: 12px;
  border: 0;
  outline: 0;
  border-radius: 6px;
  cursor: pointer;
  height: 50px;
}
.input-cont {
  display: flex;
  flex-direction: column;
}