.li-item {
    display: flex;
    width: 100%;
}
.li-name-text {
    width:20%;
    border: 1px solid #222;
    padding: 12px;
    margin: 0;
  }
  .li-mobile-no-star-text {
    width:40%;
    border: 1px solid #222;
    padding: 12px;
    margin: 0;
    display: flex;
    justify-content: space-between;
  }
  .btn-style {
      background-color: transparent;
      border: 0;
      outline: 0;
      cursor: pointer;
  }
  .star-style {
      width: 30px;
      height: 30px;
  }